// üìù ÂëΩÂêçËßÑËåÉÔºöÈ™åËØÅÁªìÊûúÁ±ªÂûã
export type ValidationResult<T> = {
  success: true
  data: T
} | {
  success: false
  error: {
    message: string
    issues: Array<{
      path: (string | number)[]
      message: string
      code: string
    }>
  }
}

// üìù ÂëΩÂêçËßÑËåÉÔºöÈ™åËØÅÂáΩÊï∞‰ΩøÁî®validateÂâçÁºÄ
export const validateCADFile = (file: unknown): ValidationResult<z.infer<typeof CADFileSchema>> => {
  try {
    const data = CADFileSchema.parse(file)
    return { success: true, data }
  } catch (error) {
    if (error instanceof z.ZodError) {
      return {
        success: false,
        error: {
          message: 'CADÊñá‰ª∂È™åËØÅÂ§±Ë¥•',
          issues: error.issues.map(issue => ({
            path: issue.path,
            message: issue.message,
            code: issue.code,
          }))
        }
      }
    }
    return {
      success: false,
      error: {
        message: 'Êú™Áü•È™åËØÅÈîôËØØ',
        issues: []
      }
    }
  }
}

