// üìù ÂëΩÂêçËßÑËåÉÔºöÈÄöÁî®È™åËØÅÂ∑•ÂÖ∑ÂáΩÊï∞
export const createValidator = <T extends z.ZodSchema>(schema: T, errorMessage: string) => {
  return (data: unknown): ValidationResult<z.infer<T>> => {
    try {
      const validData = schema.parse(data)
      return { success: true, data: validData }
    } catch (error) {
      if (error instanceof z.ZodError) {
        return {
          success: false,
          error: {
            message: errorMessage,
            issues: error.issues.map(issue => ({
              path: issue.path,
              message: issue.message,
              code: issue.code,
            }))
          }
        }
      }
      return {
        success: false,
        error: {
          message: 'Êú™Áü•È™åËØÅÈîôËØØ',
          issues: []
        }
      }
    }
  }
}

// üìù ÂëΩÂêçËßÑËåÉÔºöÂÆâÂÖ®È™åËØÅÂ∑•ÂÖ∑
export const sanitizeAndValidate = <T extends z.ZodSchema>(
  schema: T,
  data: unknown,
  sanitizers?: Array<(input: any) => any>
): ValidationResult<z.infer<T>> => {
  try {
    let sanitizedData = data
    
    // Â∫îÁî®Ê∏ÖÁêÜÂáΩÊï∞
    if (sanitizers) {
      for (const sanitizer of sanitizers) {
        sanitizedData = sanitizer(sanitizedData)
      }
    }
    
    const validData = schema.parse(sanitizedData)
    return { success: true, data: validData }
  } catch (error) {
    if (error instanceof z.ZodError) {
      return {
        success: false,
        error: {
          message: 'Êï∞ÊçÆÈ™åËØÅÂ§±Ë¥•',
          issues: error.issues.map(issue => ({
            path: issue.path,
            message: issue.message,
            code: issue.code,
          }))
        }
      }
    }
    return {
      success: false,
      error: {
        message: 'Êú™Áü•È™åËØÅÈîôËØØ',
        issues: []
      }
    }
  }
}

